@import url(https://fonts.googleapis.com/css?family=Fascinate&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-gradient(linear, left top, right top, from(rgb(94, 47, 236)), to(rgb(255, 106, 0)));
  background: -webkit-linear-gradient(left, rgb(94, 47, 236), rgb(255, 106, 0));
  background: linear-gradient(to right, rgb(94, 47, 236), rgb(255, 106, 0));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
	font-family: 'Fascinate', cursive;
	color: white;
}
